
.product{
  display: flex;
  background-color:rgb(207, 206, 204);
  z-index: 1;
  margin: 5px;
  padding: 20px;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
 max-height: 400px; 
  min-width: 100px;
  
 
 
}


.product__rating {
  display: flex;
}

.product > img {
  max-height: 150px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
  margin-top: 30px;
}

.product > button {
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.product__price {
  margin-top: 5px;
  margin-bottom: 5px;
  
}

.product__info {
  height: 100px;
  margin-bottom: 15px;
}

